import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Modal } from '@Green-Dot-Corporation/eureka-ui-modals';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
// Styles
import './styles/modal-wrapper.scss';

const { PRIMARY, SECONDARY } = VARIANTS;

class ModalWrapper extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        modalActions: PropTypes.array,
        svgType: PropTypes.oneOf(['info', 'error']),
    };

    static defaultProps = {
        modalActions: [],
    };

    render() {
        const { contextCls, children } = this.props;
        const modCls = this.getModCls();

        return (
            <Modal
                dialogContextCls={cx(this.baseCls, modCls, contextCls)}
                overlayContextCls={cx(this.baseOverlayCls, contextCls)}
                renderModalActions={this.renderModalActions}
                hasCloseButton={false}
                isOpen
            >
                {children}
            </Modal>
        );
    }

    renderModalActions = () => {
        const { modalActions } = this.props;

        const primaryAction = modalActions.find((action) => {
            return action.variant === PRIMARY;
        });
        const secondaryAction = modalActions.find((action) => {
            return action.variant === SECONDARY;
        });

        return (
            <>
                {secondaryAction && this.renderAction(secondaryAction)}
                {primaryAction && this.renderAction(primaryAction)}
            </>
        );
    };

    renderAction = (action) => {
        const { onClick, text, variant, ariaLabel } = action;
        const modCls = this.getActionsModCls();

        return (
            <Button
                contextCls={cx(`${this.baseCls}__btn`, modCls)}
                text={text}
                variant={variant}
                onClick={onClick}
                aria-label={ariaLabel}
            />
        );
    };

    getModCls = () => {
        const { svgType } = this.props;

        return {
            [`${this.baseCls}__svg--primary`]: svgType === 'info',
            [`${this.baseCls}__svg--error`]: svgType === 'error',
        };
    };

    getActionsModCls = () => {
        const { modalActions } = this.props;

        return {
            [`${this.baseCls}__btn--single`]: modalActions.length === 1,
        };
    };

    baseCls = 'modal-wrapper';

    baseOverlayCls = 'modal-wrapper__overlay';
}

export default ModalWrapper;
