import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {
    EurekaTriangleAlert,
    EurekaCircleX,
} from '@Green-Dot-Corporation/eureka-ui-icons';
import { VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import ModalWrapper from '../modal-wrapper/ModalWrapper';
import WindowContainer from '../window-container/WindowContainer';
import Option from './Option';
import Help from '../svg/Help';
import Support from '../svg/Support';
import Message from '../svg/Message';
// Styles
import './styles/choose-option-window.scss';

const { PRIMARY, SECONDARY } = VARIANTS;
class ChooseOptionWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        headerTitle: PropTypes.string,
    };

    static defaultProps = {
        // TODO: replace the title when localization available
        headerTitle: 'How can we help?',
    };

    constructor() {
        super();
        this.state = {
            shouldShowModalConfirm: false,
            shouldShowModalError: false,
        };
    }

    render() {
        const { contextCls } = this.props;
        const { shouldShowModalConfirm, shouldShowModalError } = this.state;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div className={`${this.baseCls}__header-container`}>
                    {this.renderSvg()}
                    {this.renderHeader()}
                </div>
                {this.renderOptions()}
                {shouldShowModalConfirm && this.renderModalConfirm()}
                {shouldShowModalError && this.renderModalError()}
            </WindowContainer>
        );
    }

    renderSvg = () => {
        const { contextCls } = this.props;

        return (
            <Help
                contextCls={cx(
                    `${this.baseCls}__header-container-svg`,
                    contextCls,
                )}
            />
        );
    };

    renderModalConfirm = () => {
        const { contextCls } = this.props;
        const modalActions = [
            {
                variant: PRIMARY,
                text: 'Close message',
                ariaLabel: 'Close message',
                onClick: this.handlePrimaryBtnClick,
            },
            {
                variant: SECONDARY,
                text: 'Cancel',
                ariaLabel: 'Cancel',
                onClick: this.handleSecondaryBtnClick,
            },
        ];

        return (
            <ModalWrapper
                contextCls={cx(`${this.baseCls}__modal-wrapper`, contextCls)}
                modalActions={modalActions}
                svgType="info"
            >
                <EurekaCircleX />
                {this.renderModalTitle()}
                {this.renderModalText()}
            </ModalWrapper>
        );
    };

    renderModalError = () => {
        const { contextCls } = this.props;
        const modalActions = [
            {
                variant: PRIMARY,
                text: 'OK',
                ariaLabel: 'OK',
                onClick: this.handlePrimaryBtnClick,
            },
        ];

        return (
            <ModalWrapper
                contextCls={cx(`${this.baseCls}__modal-wrapper`, contextCls)}
                modalActions={modalActions}
                svgType="error"
            >
                <EurekaTriangleAlert />
                {this.renderModalTitleError()}
                {this.renderModalTextError()}
            </ModalWrapper>
        );
    };

    renderModalTitle = () => {
        return (
            <div className="modal-wrapper__title">
                Are you sure you want to close this message?
            </div>
        );
    };

    renderModalTitleError = () => {
        return (
            <div className="modal-wrapper__title">
                The file uploaded is too large
            </div>
        );
    };

    renderModalTextError = () => {
        return (
            <div className="modal-wrapper__text">
                Please select a file smaller than 150MB for upload.
            </div>
        );
    };

    renderModalText = () => {
        return (
            <div className="modal-wrapper__text">
                Your message will not be sent. This cannot be undone.
            </div>
        );
    };

    renderHeader = () => {
        const { headerTitle } = this.props;

        return (
            <div
                className={`${this.baseCls}__header-container-title`}
                aria-label={headerTitle}
            >
                {headerTitle}
            </div>
        );
    };

    renderOptions = () => {
        return (
            <div className={`${this.baseCls}__content-container`}>
                {/* TODO: replace the title and meta when localization available */}
                <Option
                    contextCls={`${this.baseCls}__option-support`}
                    shouldShowAngle
                    text="Chat with a support agent"
                    meta="Estimated wait time 5 minutes."
                    onClick={this.handleSupportOptionClick}
                    renderSvg={this.renderSvgSupport}
                />
                {/* TODO: replace the title and meta when localization available */}
                <Option
                    contextCls={`${this.baseCls}__option-message`}
                    shouldShowAngle
                    text="Leave a message"
                    meta="Describe your issue and we will get back to you."
                    onClick={this.handleMessageOptionClick}
                    renderSvg={this.renderSvgMessage}
                />
            </div>
        );
    };

    renderSvgSupport = () => (
        <Support contextCls={`${this.baseCls}__svg-container`} />
    );

    renderSvgMessage = () => (
        <Message contextCls={`${this.baseCls}__svg-container`} />
    );

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleSupportOptionClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('support option click');
        this.setState({ shouldShowModalConfirm: true });
    };

    handleMessageOptionClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('message option click');
        this.setState({ shouldShowModalError: true });
    };

    handleSecondaryBtnClick = () => {
        this.setState({ shouldShowModalConfirm: false });
    };

    handlePrimaryBtnClick = () => {
        this.setState({
            shouldShowModalConfirm: false,
            shouldShowModalError: false,
        });
    };

    baseCls = 'choose-option-window';
}

export default ChooseOptionWindow;
