import { PureComponent, Fragment } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { ListItem } from '@Green-Dot-Corporation/eureka-ui-lists';
import { EurekaChevronRight } from '@Green-Dot-Corporation/eureka-ui-icons';
// Styles
import './styles/option.scss';

class Option extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        text: PropTypes.string,
        meta: PropTypes.string,
        shouldShowAngle: PropTypes.bool,
        onClick: PropTypes.func,
        renderSvg: PropTypes.func,
    };

    static defaultProps = {
        shouldShowAngle: false,
        onClick: () => Promise.resolve(),
        renderSvg: () => {},
    };

    render() {
        const { contextCls, text, meta, shouldShowAngle } = this.props;
        const modCls = this.getModCls();

        return (
            <div
                onClick={this.handleOptionClick}
                onKeyPress={this.handleKeyPress}
                role="button"
                tabIndex={0}
                className={`${this.baseCls}__option-container`}
            >
                <ListItem contextCls={cx(this.baseCls, modCls, contextCls)}>
                    <Fragment>
                        {this.renderSvgIcon()}
                        <div className={`${this.baseCls}__text-meta-container`}>
                            {text && (
                                <p
                                    className={`${this.baseCls}__text`}
                                    aria-label={text}
                                >
                                    {text}
                                </p>
                            )}
                            {meta && (
                                <p
                                    className={`${this.baseCls}__meta`}
                                    aria-label={meta}
                                >
                                    {meta}
                                </p>
                            )}
                        </div>
                        {shouldShowAngle && this.renderEurekaChevronRightIcon()}
                    </Fragment>
                </ListItem>
            </div>
        );
    }

    renderSvgIcon = () => {
        const { renderSvg } = this.props;

        return renderSvg();
    };

    renderEurekaChevronRightIcon = () => <EurekaChevronRight />;

    handleOptionClick = () => {
        const { onClick } = this.props;
        onClick();
    };

    handleKeyPress = () => {
        console.log('key press');
    };

    // Get Modifier Classes
    getModCls = () => {
        const { shouldShowAngle } = this.props;

        return {
            [`${this.baseCls}--cursor-pointer`]: shouldShowAngle,
            [`${this.baseCls}--cursor-default`]: !shouldShowAngle,
        };
    };

    // Base class which all component class names should be
    // derived from
    baseCls = 'web-chat-option';
}

export default Option;
